                    <span><b style="font-size: 20px;">你好，训练家，美好的一天从现在开始</b></span>
                </div>
                <div>
                    <div id="time" style="font-size: 20px;font-weight: bold;">当前时间是: {{ this.datetime }}</div>
                    <img src="@/assets/虫系打击面和抗击面.jpg" alt="" style="width: 100%;">
