</template>

<script>

export default {
    name: 'Home.vue',
    data() {
        return {
            timer: '',
            datetime: '',
            user: JSON.parse(localStorage.getItem("honey-user")||'{}')
        };
    },
    mounted(){
        /* 每秒定时刷新 */
      this.timer = setInterval(() => {
        this.datetime = this.dayjs().format("YYYY-MM-DD HH:mm:ss")
        console.log(this.datetime)
      }, 1000)
    },
    methods: {
       
    },
    beforeDestroy(){
        if(this.timer){
            clearInterval(this.timer);  //在Vue实例销毁前，清除定时器
        }
    }
};
</script>